* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #1c1c1c;
  margin: 0 auto;
  font-family: Quicksand, sans-serif;
  line-height: 1.5;
  position: relative;
  overflow: hidden;
}

p {
  margin-top: 50px;
  margin-bottom: 50px;
  margin-left: 50px;
}

p > a {
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
}

p > a:hover {
  color: #e6dbae;
}

.form {
  max-width: 500px;
  background-color: #292929;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.form label {
  color: #fff;
  margin-bottom: 10px;
  font-weight: bold;
  display: block;
}

.form input[type="number"] {
  width: 100%;
  height: 50px;
  box-sizing: border-box;
  color: #fff;
  background-color: #383838;
  border: 1px solid #ccc;
  border-radius: 4px;
  padding: 8px;
}

.form button[type="submit"] {
  width: 100%;
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  padding: 10px;
  font-weight: bold;
  display: block;
}

.form button[type="submit"]:hover {
  background-color: #0056b3;
}

